#or1{
    --a: 120deg
}
#or2{
    animation-delay: -24s;
    --a: 100deg
}

#or3{
    animation-delay: -45s;
    --a: 50deg
}

#or4{
    animation: rotate 40s linear infinite;
    animation-delay: -1.5s;
    --a: 87deg
}

#or5{
    animation-delay: -49s;
    --a: 60deg
}

#or6{
    animation-delay: -23s;
    --a: 40deg
}

#ir1{
    animation: rotate 45s linear infinite;
    --a: 120deg
}

#ir2{
    animation: rotate 45s linear infinite;
    --a: 100deg;
    animation-delay: -10s;
}

#ir3{
    animation: rotate 45s linear infinite;
    --a: 90deg;
    animation-delay: -29s;
}

#ir4{
    animation: rotate 30s linear infinite;
    --a: 80deg;
    animation-delay: -33s;
}

#ir5{
    animation: rotate 45s linear infinite;
    --a: 50deg;
    animation-delay: -43s;

}
#ir6{
    animation: rotate 45s linear infinite;
    animation-delay: -20s;
    --a: 60deg
}

#imr1{
    animation: rotate 30s linear infinite;
    --a: 120deg
}

#imr2{
    animation: rotate 30s linear infinite;
    --a: 100deg;
    animation-delay: -10s;
}

#imr3{
    animation: rotate 30s linear infinite;
    --a: 90deg;
    animation-delay: -29s;
}

#imr4{
    animation: rotate 30s linear infinite;
    --a: 80deg;
    animation-delay: -33s;
}

#imr5{
    animation: rotate 30s linear infinite;
    --a: 50deg;
    animation-delay: -43s;

}
#imr6{
    animation: rotate 30s linear infinite;
    animation-delay: -20s;
    --a: 100deg
}

@keyframes rotate{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}