body, html {
  padding: 0;
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}



#main-title{
  position: relative;
  text-align: center;
  font-weight: bold;
  background: linear-gradient(to right, rgb(255, 0, 0), rgb(20, 20, 255));
  background-clip: text;
  color: transparent;
  border-radius: 20%;
  width: fit-content;
  padding: 10px;
  margin: auto;
  margin-top: 50px;
}

#main-title::before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: rgb(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  box-shadow: 0 0 10px 10px rgb(0, 0, 0, 0.1);
  border-radius: 10px;
}


.extend-button:hover{
  border: 2px solid white;
  color: rgb(255, 255, 255, 0.5)
}

.extend-button{
  position: absolute;
  right: 0;
  background-color: darkblue;
  width: 10%;
  height: 20%;
  margin: 20px;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

#login-button{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  justify-content: center;
  height: 100px;
  width: 300px;
  margin: 20px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255, 0.05);
  border: 0px;
  color: green;
  font-size: 1.5em;
  font-weight: bold;
  transition: transform 0.5s ease;
}

#login-button:hover{
  transform: translateY(-5px);
  box-shadow: 0 0 10px 10px rgb(0, 0, 0, 0.1);
}

.pause-toggle{
  position: relative;
  background-color: rgba(0, 0, 139, 0.438);
  width: 60px;
  height: 60px;
  margin: 20px;
  border: 0px;
  border-radius: 50%;
  color: white;
}

.pause-toggle.playing::after{
  content: '';
  position: absolute;
  background-color: white;
  width: 10%;
  height: 60%;
  left: 30%;
  top: 20%;
  border-radius: 5px;
}

.pause-toggle.playing::before{
  content: '';
  position: absolute;
  background-color: white;
  width: 10%;
  height: 60%;
  right: 30%;
  top: 20%;
  border-radius: 5px;
}

.pause-toggle.paused::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 15px 0 15px 30px; /* Adjust size as needed */
  border-color: transparent transparent transparent white; /* Creates the triangle */
  left: 35%; /* Adjust position as needed */
  top: 25%; /* Adjust position as needed */
}