body{
    padding: 0;
    margin: 0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


#main-title{
  position: relative;
  text-align: center;
  font-weight: bold;
  background: linear-gradient(to right, rgb(255, 0, 0), rgb(20, 20, 255));
  background-clip: text;
  color: transparent;
  border-radius: 20%;
  width: fit-content;
  padding: 10px;
  margin: auto;
  margin-top: 50px;
}

#login{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#main-title::before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: rgb(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  box-shadow: 0 0 10px 10px rgb(0, 0, 0, 0.1);
  border-radius: 10px;
}


.extend-button:hover{
  border: 2px solid white;
  color: rgb(255, 255, 255, 0.5)
}

.extend-button{
  position: absolute;
  right: 0;
  background-color: darkblue;
  width: 10%;
  height: 20%;
  margin: 20px;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

#login-button{
    cursor: pointer;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    justify-content: center;
    height: 100px;
    width: 300px;
    margin: 20px;
    border-radius: 10px;
    background-color: rgb(0, 0, 0, 0.7);
    border: 0px;
    color: green;
    font-size: 1.5em;
    font-weight: bold;
    transition: transform 0.5s ease, background-color 0.5s ease;
    margin-bottom: 300px;
}

#login-button:hover{
  transform: translateY(-5px);
  box-shadow: 0 0 10px 10px rgb(0, 0, 0, 0.1);
  background-color: black;
}