.top{
    z-index: 0;
    background: linear-gradient(to bottom, #971a18, #75141c); /* Same gradient as .nav */
    width: fit-content;
    height: fit-content;
    border: 0px;
    position: fixed;
    top: 15px;
    transition: right 0.5s ease;
    border: 1px solid white;
    border-right: transparent;
    border-radius: 10px 0 0 10px;
}

.top:hover{
    opacity: 0.5;
    cursor: pointer;
}

.nav {
    border-left: 1px solid white;
    z-index: 1;
    height: 100vh;
    width: 200px;
    background: linear-gradient(to bottom, rgb(255, 0, 0, 0.5), rgb(20, 20, 255, 0.5));    
    position: fixed;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    right: -200px;
    transition: right 0.5s ease;
}

.nav.visible {
    right: 0; /* Slide in */
}

.content{
    margin-top: 70px;
}

.content > *{
    text-align: left;
    margin: 10px;
    width: 100%;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
   
}