.background{
    background-color: darkblue;
    background-image: url('../../../public/suno-extender-background.jpg');
    background-size: cover;
    z-index: -10;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    align-items: center;
    justify-content: center;
    display: flex;
}

.container{
    width: 20vw;
    height: 20vw;
    position: relative;
}

.outerRing{
    width: 300px;
    height: 300px;
    border-radius: 50%;
    position: absolute;
    overflow: hidden;
    z-index: -1;
}

.innerRing{
    width: 240px;
    height: 240px;
    border-radius: 50%;
    position: absolute;
    overflow: hidden;
}

.innermostRing{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: absolute;
    overflow: hidden;
}

.outerRing > .ring{
    width: 300px;
    background: darkblue;
    size: fit-content;
}

.innerRing > .ring{
    width: 240px;
    background: blue;
    size: fit-content;
}

.innermostRing > .ring{
    width: 200px;
    background: cyan;
    size: fit-content;
}

.ring{
    background: white;
    position: absolute;
    aspect-ratio: 1;
    padding: 5px;
    box-sizing: border-box;
    border-radius: 50%;
    opacity: 0.4;
    mask:
      linear-gradient(#0000 0 0) content-box intersect,
      conic-gradient(#000 var(--a),#0000 0);
    animation: rotate 60s linear infinite;
  }

@keyframes rotate{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}